#slide {
  background: url("/img/header/bg-1.png") no-repeat top center;
  background-size: 100% auto;
  min-height: 930px;
  padding-bottom: 140px;
  display: flex;
  align-items: center;

  .slide {
    display: flex;
    justify-content: space-between;

    .info {
      //margin: auto auto auto 130px;
      //margin: auto auto auto 40px;

      h1 {
        color: $azure-color;
        font-size: 40px;
        font-weight: 700;
      }
    }
  }

  .button {
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
    text-align: center;

    a {
      &:first-child {
        margin-right: 30px;
      }
    }
  }

  @media screen and (max-width: 1199px) {
    min-height: 700px;
    align-items: flex-start;
    padding-top: 190px;
    padding-bottom: 180px;
    background-size: cover;

    .slide .info h1 {
      font-size: 36px;
    }
  }

  @media screen and (max-width: 991px) {
    background-size: cover;
    min-height: 1000px;
    padding-top: 195px;

    .slide {
      flex-direction: column;

      .img {
        order: 1;
      }

      .info {
        order: 2;

        .button {
          margin-top: 10px;
        }
      }
    }
  }

  @media screen and (max-width: 767px) {
    align-items: flex-start;
    min-height: auto;
    padding-top: 210px;
    padding-bottom: 85px;

    .slide {
      flex-direction: column;

      .info {
        margin: 0;

        h1 {
          text-align: center;
        }

        .button {
          margin-top: 50px;
        }
      }
    }
  }

  @media screen and (max-width: 575px) {
    padding-top: 200px;

    .slide {
      .info {
        h1 {
          font-size: 28px;
        }
      }
    }
  }
}