footer {
  background-color: #212e30;
  padding: 50px 0;

  .nav {
    color: #437077;
    font-size: 14px;
    font-weight: 400;

    ul {
      padding: 0;

      li {
        list-style: none;
        line-height: 30px;

        a {
          color: #437077;
          font-size: 14px;
          font-weight: 400;

          &:hover {
            color: #437077;
            text-decoration: none;
            font-weight: 600;
          }
        }
      }
    }
  }

  .social {
    display: flex;
    justify-content: space-between;
  }

  @media screen and (max-width: 1199px) {}

  @media screen and (max-width: 991px) {
    .logo {
      text-align: center;
      margin-bottom: 20px;
    }

    .social {
      justify-content: space-evenly;
      margin-top: 20px;
    }

    .nav {
      margin-top: 20px;

      ul {
        margin-bottom: 0;

        li {
          text-align: center;
        }
      }
    }
  }
}