#nike {
  text-align: center;
  background: url("/img/nike/bg-4.png");
  margin: -260px 0 0 0;
  padding-top: 300px;
  padding-bottom: 100px;

  .shue-full-info {
    text-align: left;
    margin-top: 100px;

    .shue-info {
      h3 {
        font-size: 35px;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 0;
      }

      p {
        font-size: 20px;
        font-weight: 300;
        text-transform: uppercase;
        margin-bottom: 0;
      }

      .price {
        margin-top: 10px;

        span {
          color: #f50057;
          font-size: 35px;
          font-weight: 700;
        }

        a.azure-full {
          display: inline-block;
          margin-left: 30px;
        }
      }
    }

    .review {
      text-align: end;

      p {
        color: $azure-color;
        margin-right: 40px;
        font-size: 18px;
        font-weight: 500;
      }
    }
  }

  @media screen and (max-width: 1199px) {
    margin: -340px 0 0;
    padding-top: 250px;
  }

  @media screen and (max-width: 991px) {
    margin: -190px 0 0;
    padding-top: 250px;
    padding-bottom: 55px;

    .shue-full-info {
      margin-top: 55px;
    }
  }

  @media screen and (max-width: 767px) {
    padding-top: 210px;
    padding-bottom: 35px;

    .shue-full-info {
      margin-top: 40px;

      .shue-info {
        h3 {
          font-size: 20px;
        }

        p {
          font-size: 18px;
        }
      }

      .review {
        margin-top: 20px;
      }
    }
  }

  @media screen and (max-width: 575px) {
    padding-top: 200px;
    padding-bottom: 10px;

    .shue-full-info {
      margin-top: 40px;
      flex-direction: column;

      .shue-info {
        h3 {
          font-size: 18px;
        }

        p {
          font-size: 16px;
        }
      }

      .review {
        margin-top: 20px;
      }
    }
  }
}