#way {
  background: url("/img/way/bg-3-test.png") no-repeat top center;
  //background-size: 100% auto;
  height: 900px;
  padding-top: 450px;
  padding-bottom: 550px;

  .way-text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    h2 {
      color: #263238;
      font-size: 50px;
      font-weight: 700;
    }

    p {
      color: #263238;
      font-size: 20px;
      font-weight: 300;
      text-align: center;
    }
  }

  @media screen and (max-width: 1199px) {
    padding-top: 315px;
    padding-bottom: 0;

    .way-text {
      h2 {
        font-size: 40px;
      }
    }
  }

  @media screen and (max-width: 991px) {
    padding-top: 230px;
  }

  @media screen and (max-width: 767px) {
    height: auto;
    padding-bottom: 230px;

    .way-text {
      h2 {
        font-size: 50px;
      }

      p {
        font-size: 18px;
      }
    }
  }

  @media screen and (max-width: 575px) {
    height: auto;
    padding-top: 200px;
    padding-bottom: 180px;

    .way-text {
      h2 {
        font-size: 30px;
      }

      p {
        font-size: 18px;
      }
    }
  }
}