body {
  font-family: 'Roboto', sans-serif;
  color: #263238;

  img {
    max-width: 100%;
    object-fit: scale-down;
  }

  .azure-full {
    text-transform: uppercase;
    color: #ffffff;
    font-weight: 700;
    width: 200px;
    box-shadow: 0 10px 30px rgba(20, 108, 113, 0.15), inset 0 0 2px rgba(255, 255, 255, 0.3);
    border-radius: 25px;
    background-color: $azure-color;
    border: none;
    padding: 10px 0;
  }

  .azure-empty {
    text-transform: uppercase;
    color: #ffffff;
    font-weight: 700;
    width: 200px;
    box-shadow: 0 10px 30px rgba(20, 108, 113, 0.15), inset 0 0 2px rgba(255, 255, 255, 0.3);
    border-radius: 25px;
    //background-color: $azure-color;
    border: 2px solid #ffffff;
    padding: 10px 0;
  }

  a.azure-full, a.azure-empty {
    cursor: pointer;
    display: block;
    text-align: center;

    &:hover {
      color: #ffffff;
      text-decoration: none;
      box-shadow: 0 0 0 2px white, 0 0 0 4px $azure-color;
    }
  }
}