#shue {
  background: url(/img/shue/bg-2.png) no-repeat top center;
  background-size: 100% auto;
  //background-size: cover;
  margin: -400px 0 0 0;
  padding-bottom: 20px;
  text-align: center;

  .container {
    padding-top: 250px;
  }

  @media screen and (max-width: 1199px) {
    margin: -240px 0 0;
    padding-bottom: 20px;

    .container {
      padding-top: 130px;
    }
  }

  @media screen and (max-width: 991px) {
    margin: -135px 0 0 0;

    .container {
      padding-top: 150px;
    }
  }

  @media screen and (max-width: 767px) {
    margin: -105px 0 0 0;

    .container {
      padding-top: 90px;
    }

    a.azure-full {
      margin: 25px auto auto;
    }
  }

  @media screen and (max-width: 575px) {
    margin: -80px 0 0 0;
  }
}